@import "var";
@import "mixin";

body {
    @include txt-normal;
    
    color: $color-txt;
    font-size: $size-txt-mid;
    margin: 0;
    padding: 0;
}

#app {
    display: grid;
    position: absolute;
    width: 100%;
    height: 100%;
    grid-template-rows: auto 1fr auto;
}

#app-header {
    grid-row: 1;
    color: $color-txt;
    background: $color-bg-mid-lt;
    padding: $size-pad-lg;

    h1 {
        margin: $size-pad-lg 0 $size-pad-mid 0;
        font-size: $size-txt-lg;
    }
}

#app-content {
    grid-row: 2;
    overflow: auto;
    padding: $size-pad-lg;

    h2 {
        font-size: $size-txt-mid-lg;
        margin: $size-pad-lg 0 $size-pad-mid 0;
        padding: $size-pad-mid $size-pad-lg - $size-pad-mid;
        border-left: $size-pad-mid solid;
        border-bottom: 1px solid;
        color: $color-bg-mid-dk;
    }
}

.struct-table {
    @include txt-code;

    th, td {
        white-space: nowrap;
        padding: $size-pad-sm $size-pad-lg;
    }

    th {
        @include txt-bold;
        text-align: left;
    }
}

#app-footer {
    grid-row: 3;
    color: $color-txt-lt;
    background: $color-bg-dk;
    padding: $size-pad-sm $size-pad-lg;
    white-space: nowrap;
    font-size: $size-txt-sm;

    a {
        color: $color-txt-lt;
    }

    &>div {
        display: inline-block;
    }
}